@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Nunito:wght@300;400;500;600;700;900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
}

.App  {
    background-color: #98afff;
    height: 100vh;
    display: flex;
    align-items: center;
}
.container {
    padding: 20px;
    margin: 0 auto;
    height: 600px;
    min-width: 500px;
    border-radius: 5%;

    background-color: #3945a3;
}

h1 {
    text-align: center;
    color: #fff;
}

.header {
    display: flex;
    justify-content: center;
    max-width: 100%;
    margin: 15px 0px 20px;
    position: relative;
}

.button__add {
    background-color: transparent;
    position: absolute;
    right: 0;
    border: none;
    width: auto;
    height: 100%;
    opacity: 0.2;
}

.input {
    width: 100%;
    background-color: transparent;
    padding: 15px 18px;
    border: solid 1px hsl(242, 12%, 42%);
    border-radius: 25px;
    font-size: 15px;
}

.input:focus{
    outline: 1.5px solid  hsl(242, 12%, 42%);
    padding: 16px 20px;
    }

.task-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: #0A155A;
    margin: 15px 0px;
    padding: 13px 0px 13px 15px;
    border-radius: 25px;
}

.label {
    background-color: #0A155A;
}

.todo {
    background-color: #0A155A;
}

.done {
    text-decoration: line-through ;
}

.delete-btn {
    margin-right: 10px;
    height: 30px;
}

.custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.custom-checkbox+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.custom-checkbox+label::before {
  content: '';
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #D103FC;
  border-radius: 50%;
  margin-right: 1em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.custom-checkbox:checked+label::before {
    border-color: #22278b;
    background-color: #22278b;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
  }


